import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Processo_fluxo } from '../../../modelo/Processo_fluxo'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'processo_fluxo-dados-edit',   templateUrl: 'processo_fluxo_dadosEdit.component.html' }) export class Processo_fluxoDadosEditComponent implements OnInit {    processo_fluxo: Processo_fluxo;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.processo_fluxo.Processo_fluxo";                 let id = +params['id'];         this.processo_fluxo = new Processo_fluxo();         this.processo_fluxo.cd_processo_fluxo = id;         o.objetoJson = this.processo_fluxo;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a processo_fluxo. ';       }      this.processo_fluxo = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


